* {
  box-sizing: border-box;
}

@font-face {
  font-family: "BabelStone Xiangqi";
  src: local("BabelStone Xiangqi"), url("./../fonts/BabelStoneXiangqi.ttf") format("truetype");
}

.xq{
  font-family: "BabelStone Xiangqi";
}

.xq-king::before{
  content: "\1FA60";  /*\U+1FA60 -> \1FA60*/
}

.container{
  margin: 10px auto;
}

.chess-board{
  font-family: "BabelStone Xiangqi";
  background: #e7e3bb;
  padding: 0 10px;
  width: 284px; /*padding * 2 + 24px * 11=264px*/
  float: left;
}

.layer{
  font-size:24px;
  line-height:24px; /*same font size to reduce space between rows*/
}

.square{
  display: inline-block;
  position: relative;
  /* border: 0.5px dotted #fd00ff; */
}

.chess{
  background: #e7e3bb;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 16px;
}

.navigation {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 284px;
  border: 1px solid;
  padding: 10px;
}

.navigation button {
  display: inline-block;
  font-size: 36px;
  width: 48px;
  border: 1px solid;
  text-align: center;
  cursor: pointer;
}

.history {
  border: 1px dashed;
  float: left;
  width: 350px;
  padding: 5px;
}

.history .moves{
  height: 148px;
  overflow: auto;
  margin: 10px;
}

.history ul{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 0;
}

.history .moves li{
  width: 70px;
  /* border: 1px dashed; */
  list-style: none;
  margin:0;
  margin-right:5px;
  padding:0;
  cursor: pointer;
}

.history .moves .current{
  background: #daec9b;
}

.line-number{
  text-align: center;
  margin: 0;
  padding: 0;
  position: relative;
}

  .line-number.top{
    bottom: -14px;
  }

  .line-number.bottom{
    bottom: 14px;
  }

  .line-number .number{
    font-size: 12px;
    padding: 0 9px;
    display: inline-block;
    text-align: center;
  }

.tab{
  display: block;
}

.tab ul{
  margin:0;
  padding:0;
  list-style: none;
}

.tab > ul li{
  display: inline-block;
  border-bottom: 2px solid #9f9f9f;
  margin-bottom: 10px;
  padding: 2px 5px;
  color: #9f9f9f;
  cursor: pointer;
}

.tab > ul li.current{
  border-bottom: 2px solid #333;
  color: #333;
}
